<template>
    <Lay :route="route" :arr="arr" :url="url"/>
</template>

<script>
  import Lay from '@/components/common/template';
  import {powerdrill} from '@/config/record'

  export default {
    props: ["route"],
    data() {
      return {
        url: '/data/query/dynamicdrillingtooldata',
        arr: powerdrill
      }
    },
    components: {
      Lay
    },
    mounted: function () {},
    methods: {}
  }
</script>



